import { Component, OnInit, Input } from '@angular/core';
import { Action } from '../../../shared/common/action';
import { CompanyService, Company } from '../../entities/company/company.service';
import { Router, ActivatedRoute } from '@angular/router';
import { FormGroup, FormBuilder, Validators, FormControl } from '@angular/forms';
import { ToastrService } from 'ngx-toastr';
import { Observable } from 'rxjs';
import { Transporter } from '../../../shared/models/transporter';
import { TransporterService } from '../../../shared/services/transporter.service';

@Component({
  selector: 'app-transporter-editor',
  templateUrl: './transporter-editor.component.html',
  styleUrls: ['./transporter-editor.component.scss']
})
export class TransporterEditorComponent implements OnInit {

  @Input() transporterId: number;
  @Input() action: Action;

  header = '';
  showProgress = false;
  submitted = false;

  // To validate if any services is selected during submitting the form.
  noServicesSelected = false;

  transporterForm: FormGroup;
  loading: boolean;

  private transporter: Transporter = new Transporter({
    address : {addressLine: '', city: '', state: '', country: '', postCode: ''},
    status: 'ACTIVE'
  });

  constructor(public service: TransporterService,
    private router: Router,
    private route: ActivatedRoute,
    private fb: FormBuilder,
    private toastr: ToastrService) {
      this.setForm(this.transporter);
    }

  ngOnInit(): void {
    this.header = this.action === Action.ADD || this.action === Action.CLONE ? 'New Transporter' : 'Update Transporter';
    this.route.params.subscribe(params => this.transporterId = params['id']);

    if (this.transporterId) {
      this.showProgress = true;
      this.service.getAllTransporterById(this.transporterId).subscribe(
        res => {
          this.transporter = res;
          if (this.isClone()) {
            this.setFormForClone(this.transporter);
          } else {
            this.setForm(this.transporter);
          }
        },
        error => {
          this.toastr.error('Error occured : ' + error.errorMessage, 'Failed');
        }
      );
    }


  }

  isClone(): boolean {
    return this.action === Action.CLONE;
  }

  cancel() {
    this.router.navigate(['entities/transporter']);
  }

  get f() { return this.transporterForm.controls; }


  async setForm(transporter: Transporter) {
    this.transporterForm = this.fb.group({
      id: this.action === Action.EDIT ? transporter.id : null,
      name: [transporter.name, [Validators.required]],
      companyIdentificationNumber: [transporter.companyIdentificationNumber, [Validators.required]],
      address: this.fb.group({
        addressLine: transporter.address.addressLine,
        city: transporter.address.city,
        state: transporter.address.state,
        country: transporter.address.country,
        postCode: transporter.address.postCode
       }
      ),
      primaryPhoneNo: transporter.primaryPhoneNo,
      secondaryPhoneNo: transporter.secondaryPhoneNo,
      email: [transporter.email,  [Validators.required, Validators.email]],
      status: transporter.status,
      licenseNumber: transporter.licenseNumber,
      attachment: [transporter.id? transporter.attachment : null],
      categoryOfService: transporter.categoryOfService
    });
    this.showProgress = false;
  }

  async setFormForClone(transporter: Transporter) {
    this.transporterForm = this.fb.group({
      id: null,
      name: ['', [Validators.required]],
      companyIdentificationNumber: ['', [Validators.required]],
      routingNumber: ['', [Validators.required]],
      address: this.fb.group({
        addressLine: transporter.address.addressLine,
        city: transporter.address.city,
        state: transporter.address.state,
        country: transporter.address.country,
        postCode: transporter.address.postCode
       }
      ),
      primaryPhoneNo: transporter.primaryPhoneNo,
      secondaryPhoneNo: transporter.secondaryPhoneNo,
      email: [transporter.email,  [Validators.required, Validators.email]],
      status: transporter.status,
      licenseNumber: transporter.licenseNumber,
      attachment: [transporter.id? transporter.attachment : null],
      categoryOfService: transporter.categoryOfService
    });
    this.showProgress = false;
  }

  

  save() {
    this.submitted = true;
    const transporter: Transporter = <any>this.transporterForm.value;

    if (this.transporterForm.invalid) {
      return;
    }
    
    this.showProgress = true;    
    
    let resp: Observable<any>;
    let alertMessage: string =  '';

    if (this.action === Action.ADD || this.action === Action.CLONE) {
      resp = this.service.create(transporter);
      alertMessage = "Transporter created successfully !";
      this.service.loadTransporterData(0, 6);
    }

    if (this.action === Action.EDIT) {
      resp = this.service.update(transporter);
      alertMessage = "Transporter updated successfully !";
    }

    resp.subscribe(response => {
        if (response) {
          this.toastr.success(alertMessage, 'Success', { timeOut: 30000 });
          this.service.onTransporterSelected.next(response.data);
          this.showProgress = false;
        } else {
          this.toastr.error(response.errorMessage, 'Failed', { timeOut: 30000});
          this.showProgress = false;
        }
      },
      error => {
        this.toastr.error(error.errorMessage, 'Failed', { timeOut: 30000});
        this.showProgress = false;
      }
    );
  }

}
