import { Component, OnInit, Input } from '@angular/core';
import { Action } from '../../../shared/common/action';
import { CompanyService, Company } from '../../entities/company/company.service';
import { Router, ActivatedRoute } from '@angular/router';
import { FormGroup, FormBuilder, Validators, FormControl } from '@angular/forms';
import { ToastrService } from 'ngx-toastr';
import { Observable } from 'rxjs';
import { FinancialService } from '../../../shared/services/financial.service';
import { Financial } from '../../../shared/models/financial';

@Component({
  selector: 'app-financial-editor',
  templateUrl: './financial-editor.component.html',
  styleUrls: ['./financial-editor.component.scss']
})
export class FinancialEditorComponent implements OnInit {

  @Input() financialId: number;
  @Input() action: Action;

  header = '';
  showProgress = false;
  submitted = false;

  // To validate if any services is selected during submitting the form.
  noServicesSelected = false;

  financialForm: FormGroup;
  loading: boolean;

  private financial: Financial = new Financial({
    address : {addressLine: '', city: '', state: '', country: '', postCode: ''},
    status: 'ACTIVE'
  });

  constructor(public service: FinancialService,
    private router: Router,
    private route: ActivatedRoute,
    private fb: FormBuilder,
    private toastr: ToastrService) {
      this.setForm(this.financial);
    }

  ngOnInit(): void {
    this.header = this.action === Action.ADD || this.action === Action.CLONE ? 'New Financial Institute' : 'Update Financial Institute';
    this.route.params.subscribe(params => this.financialId = params['id']);

    if (this.financialId) {
      this.showProgress = true;
      this.service.getFinancialById(this.financialId).subscribe(
        res => {
          this.financial = res;
          if (this.isClone()) {
            this.setFormForClone(this.financial);
          } else {
            this.setForm(this.financial);
          }
        },
        error => {
          console.log('Error occured: ' + error);
        }
      );
    }


  }

  isClone(): boolean {
    return this.action === Action.CLONE;
  }

  cancel() {
    this.router.navigate(['entities/financial']);
  }

  get f() { return this.financialForm.controls; }


  async setForm(financial: Financial) {
    this.financialForm = this.fb.group({
      id: this.action === Action.EDIT ? financial.id : null,
      name: [financial.name, [Validators.required]],
      routingNumber: [financial.routingNumber, [Validators.required]],
      address: this.fb.group({
        addressLine: financial.address.addressLine,
        city: financial.address.city,
        state: financial.address.state,
        country: financial.address.country,
        postCode: financial.address.postCode
       }
      ),
      primaryPhoneNo: financial.primaryPhoneNo,
      secondaryPhoneNo: financial.secondaryPhoneNo,
      email: [financial.email,  [Validators.required, Validators.email]],
      status: financial.status
    });
    this.showProgress = false;
  }

  async setFormForClone(financial: Financial) {
    this.financialForm = this.fb.group({
      id: null,
      name: ['', [Validators.required]],
      routingNumber: ['', [Validators.required]],
      address: this.fb.group({
        addressLine: financial.address.addressLine,
        city: financial.address.city,
        state: financial.address.state,
        country: financial.address.country,
        postCode: financial.address.postCode
       }
      ),
      primaryPhoneNo: financial.primaryPhoneNo,
      secondaryPhoneNo: financial.secondaryPhoneNo,
      email: [financial.email,  [Validators.required, Validators.email]],
      status: financial.status
    });
    this.showProgress = false;
  }

  

  save() {
    this.submitted = true;
    const financial: Financial = <any>this.financialForm.value;

    if (this.financialForm.invalid) {
      return;
    }
    
    this.showProgress = true;    
    
    let resp: Observable<any>;
    let alertMessage: string =  '';

    if (this.action === Action.ADD || this.action === Action.CLONE) {
      resp = this.service.create(financial);
      alertMessage = "Financial institute created successfully !";
      this.service.loadFinancialData(0, 6);
    }

    if (this.action === Action.EDIT) {
      resp = this.service.update(financial);
      alertMessage = "Financial institute updated successfully !";
    }

    resp.subscribe(response => {
        if (response) {
          this.toastr.success(alertMessage, 'Success', { timeOut: 30000 });
          this.service.onFinancialSelected.next(response.data);
          this.showProgress = false;
        } else {
          this.toastr.error(response.errorMessage, 'Failed', { timeOut: 30000});
          this.showProgress = false;
        }
      },
      error => {
        this.toastr.error(error.errorMessage, 'Failed', { timeOut: 30000});
        this.showProgress = false;
      }
    );
  }
}
