import { Component, OnInit, Input } from '@angular/core';
import { Action } from 'src/app/shared/common/action';
import { RetailerService, Retailer } from '../../entities/retailer/retailer.service';
import { Router, ActivatedRoute } from '@angular/router';
import { FormGroup, FormBuilder } from '@angular/forms';
import { ToastrService } from 'ngx-toastr';

@Component({
  selector: 'app-retailer-editor',
  templateUrl: './retailer-editor.component.html',
  styleUrls: ['./retailer-editor.component.scss']
})
export class RetailerEditorComponent implements OnInit {

  @Input() retailerId: number;
  @Input() action: Action;

  header = '';

  retailerForm: FormGroup;
  loading: boolean;

  public retailer: Retailer = new Retailer({
    address : {addressLine: '', city: '', state: '', country: '', postCode: ''},
  });

  constructor(public retailerService: RetailerService,
    private router: Router,
    private route: ActivatedRoute,
    private fb: FormBuilder,
    private toastr: ToastrService) {
      this.setForm(this.retailer);
    }

  ngOnInit(): void {
    this.header = this.action === Action.ADD ? 'New Retailer' : 'Update Retailer';
    this.route.params.subscribe(params => {
     console.log('param', params);
      this.retailerId = params['id'];
    });

    if (this.retailerId) {
      this.retailerService.getRetailerById(this.retailerId).subscribe(
        res => {
          console.log('result', res);
          this.retailer = res;
          this.setForm(this.retailer);
        }
      );
    }
  }

  cancel() {
    this.router.navigate(['entities/retailer']);
  }


  async setForm(retailer: Retailer) {
    this.retailerForm = this.fb.group({
      id: retailer.id,
      retailerCompany: retailer.retailerCompany,
      name: retailer.name,
      registrationNumber: retailer.registrationNumber,
      address: this.fb.group({
        addressLine: retailer.address.addressLine,
        city: retailer.address.city,
        state: retailer.address.state,
        country: retailer.address.country,
        postCode: retailer.address.postCode,
        longitude: retailer.address.longitude,
        lattitude: retailer.address.lattitude
       }
      ),
      primaryPhone: retailer.primaryPhoneNo,
      secondaryPhoneNo: retailer.secondaryPhoneNo,
      email: retailer.email,
      primaryIMEI: retailer.primaryIMEI,
      secondaryIMEI: retailer.secondaryIMEI,

      stationManagerName: retailer.stationManagerName,
      assistantStationManagerName: retailer.assistantStationManagerName,
      contractId: retailer.contractId,
      contractStartDate: retailer.contractStartDate,
      contractEndDate: retailer.contractEndDate,
      region: retailer.region,

    });
    console.log("retailer", retailer);
  }

  onFileChange(event) {
    const reader = new FileReader();
    if (event.target.files && event.target.files.length > 0) {
      const file = event.target.files[0];
      reader.readAsDataURL(file);
      reader.onload = () => {
        this.retailerForm.get('attachment').setValue({
          id: null,
          name: file.name,
          content: reader.readAsText(file)
        });
      };
    }
  }

  onCompanyChange(event) {

  }

}
