import { Component, OnInit, Input } from '@angular/core';
import { Action } from '../../../shared/common/action';
import { CompanyService, Company } from '../../entities/company/company.service';
import { Router, ActivatedRoute } from '@angular/router';
import { FormGroup, FormBuilder, Validators, FormControl } from '@angular/forms';
import { ToastrService } from 'ngx-toastr';
import { Observable } from 'rxjs';
import { DataLayerService } from 'src/app/shared/services/data-layer.service';
import { HttpErrorResponse } from '@angular/common/http';

@Component({
  selector: 'app-company-editor',
  templateUrl: './company-editor.component.html',
  styleUrls: ['./company-editor.component.scss']
})
export class CompanyEditorComponent implements OnInit {

  @Input() companyId: number;
  @Input() action: Action;

  header = '';
  showProgress = false;
  submitted = false;
  countries$ : any[];

  fileName = '';

  // to validate if registration attachment provided.
  noAttachment = false;
  // To validate if any services is selected during submitting the form.
  noServicesSelected = false;

  companyForm: FormGroup;
  loading: boolean;

  private company: Company = new Company({
    address : {addressLine: '', city: '', state: '', country: '', postCode: ''},
    status: 'ACTIVE'
  });

  constructor(public companyService: CompanyService,
    private router: Router,
    private route: ActivatedRoute,
    private dl: DataLayerService,
    private fb: FormBuilder,
    private toastr: ToastrService) {
      this.setForm(this.company);
    }

  ngOnInit(): void {
    this.header = this.action === Action.ADD || this.action === Action.CLONE ? 'New Company' : 'Update Company';
    this.route.params.subscribe(params => this.companyId = params['id']);
    this.dl.getCountries().subscribe (
      response => {
        this.countries$ = response;
      }
    )
      

    if (this.companyId) {
      this.showProgress = true;
      this.companyService.getCompanyById(this.companyId).subscribe(
        res => {
          this.company = res;
          if (this.isClone()) {
            this.setFormForClone(this.company);
          } else {
            this.setForm(this.company);
            this.fileName = this.company.attachment? this.company.attachment.name: '';
          }
        },
        error => {
          console.log('Error occured: ' + error);
        }
      );
    }


  }

  isClone(): boolean {
    return this.action === Action.CLONE;
  }

  cancel() {
    this.router.navigate(['entities/company']);
  }

  get f() { return this.companyForm.controls; }


  async setForm(company: Company) {
    this.companyForm = this.fb.group({
      id: this.action === Action.EDIT ? company.id : null,
      name: [company.name, [Validators.required]],
      registrationNumber: [company.registrationNumber, [Validators.required]],
      address: this.fb.group({
        addressLine: company.address.addressLine,
        city: company.address.city,
        state: company.address.state,
        country: company.address.country,
        postCode: company.address.postCode
       }
      ),
      attachment: [company.id? company.attachment : null, [Validators.required]],
      contactPersonName: company.contactPersonName,
      primaryPhoneNo: company.primaryPhoneNo,
      email: [company.email,  [Validators.required, Validators.email]],
      companyServices: new FormControl(company? company.companyServices : null, Validators.required),
      status: company.status
    });
    this.showProgress = false;
  }

  async setFormForClone(company: Company) {
    this.companyForm = this.fb.group({
      id: null,
      name: ['', [Validators.required]],
      registrationNumber: ['', [Validators.required]],
      address: this.fb.group({
        addressLine: company.address.addressLine,
        city: company.address.city,
        state: company.address.state,
        country: company.address.country,
        postCode: company.address.postCode
       }
      ),
      attachment: [null, [Validators.required]],
      contactPersonName: company.contactPersonName,
      primaryPhoneNo: company.primaryPhoneNo,
      email: [company.email,  [Validators.required, Validators.email]],
      companyServices: new FormControl(company? company.companyServices : null, Validators.required),
      status: company.status
    });
    this.showProgress = false;
  }

  onFileChange(event) {
    this.fileName = '';
    const reader = new FileReader();
    if (event.target.files && event.target.files.length > 0) {
      const file = event.target.files[0];
      reader.readAsDataURL(file);
      reader.onload = () => {
        this.companyForm.get('attachment').setValue({
          id: null,
          name: file.name,
          content: (<string>reader.result).split(',')[1]
        });
      };
    }
  }

  save() {
    this.submitted = true;
    const company: Company = <any>this.companyForm.value;

    if (this.companyForm.invalid) {
      if (!this.company.attachment) {
        this.noAttachment = true;
      }

      if (!this.company.companyServices) {
        this.noServicesSelected = true;
      }
      return;
    }

    this.showProgress = true;    
    let resp: Observable<any>;
    let alertMessage: string =  '';

    if (this.action === Action.ADD || this.action === Action.CLONE) {
      resp = this.companyService.create(company);
      alertMessage = "Company created successfully";
      this.companyService.loadCompanyData(0, 6);
    }

    if (this.action === Action.EDIT) {
      resp = this.companyService.update(company);
      alertMessage = "Company updated successfully";
    }

    resp.subscribe(response => {
      
        if (response.status) {
          this.toastr.success(alertMessage, 'Success', { timeOut: 30000 });
          this.companyService.onCompanySelected.next(response.data);
          this.showProgress = false;
        } else {
          this.toastr.error(response.errorMessage, 'Failed', { timeOut: 30000});
          this.showProgress = false;
        }
      },
      error => {
        const err = JSON.parse(error.error);
        this.toastr.error(err.errorMessage, 'Failed', { timeOut: 30000});
        this.showProgress = false;
        return;
      }
    );
  }
}
