import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable, BehaviorSubject, Subject, of, combineLatest } from 'rxjs';
import { map, switchMap } from 'rxjs/operators';
import { Address, Attachment } from 'src/app/model/common-model';
import { WebApiProvider } from 'src/app/shared/providers';
import { ToastrService } from 'ngx-toastr';

export class Company {
    id: string;
    name: string;
    registrationNumber: string;
    contactPersonName: string;
    primaryPhoneNo: string;
    email: string;
    status: string;
    address: Address;
    companyServices: string[];
    attachment: Attachment;

    public constructor(init?: Partial<Company>) {
      Object.assign(this, init);
  }
}

export class CompanySearch {
  name?: string;
  registrationNumber?: string;
  status?: string;
}


export class CompanyResponse {
  totalPages: number;
  totalElements: number;
  content: Company[];
}

@Injectable({
  providedIn: 'root'
})
export class CompanyService {

  public companies: Company[];
  public company: Company;
  public collectionSize: number;

  onContactSelected = new BehaviorSubject<any>(null);

  onCompanySelected = new BehaviorSubject<any>(null);
  onAdvancedSearchApplied = new BehaviorSubject<any>(null);
  onClearSearchApplied = new BehaviorSubject<any>(null);

  onChatsUpdated = new Subject<any>();
  loadingCollection: boolean;
  loadingData: boolean;

  constructor(private http: HttpClient,
    private toastr: ToastrService,
    private webApiProvider: WebApiProvider) {
  }

  loadCompanyData(page: number, size: number): Observable<CompanyResponse> {
    return this.getAllCompanies((page - 1), size);
  }

  create(company: Company) {
    return this.webApiProvider.post('company/v1/', company, true);
  }

  update(company: Company): Observable<Company> {
    return this.webApiProvider.put('company/v1/' + company.id, company);
  }

  delete(id: string): Observable<any> {
    return this.webApiProvider.delete('company/v1/'+ id, true);
  }

 
  getAllCompanies(page, size): Observable<CompanyResponse> {
    return this.webApiProvider.post<any>('company/v1/list/' + page + '/'+ size, null, true);
  }

  getCompanyById(id): Observable<Company> {
    this.loadingCollection = true;
    this.loadingData = true;
    return this.getAllCompanbyID(id)
      .pipe(map(company => {
        this.onCompanySelected.next({
          company: company
        });
        this.loadingCollection = false;
        this.loadingData = false;
        return company;
      }));
  }

  autoReply(chat) {
    setTimeout(() => {
      this.onChatsUpdated.next(chat);
    }, 1500);
  }

  getAllCompanbyID(id): Observable<Company> {
    return this.webApiProvider.get<Company>(`company/v1/${id}`);
  }

  performAdvancedSearch(query: string): void {
    this.loadingCollection = true;
    this.webApiProvider.post<CompanyResponse>('company/v1/list/0/6'+ query, null, true)
    .subscribe (response => {
      if (response) {
        this.onAdvancedSearchApplied.next({
          companies: response
        });
        this.loadingCollection = false;
      }
    },
     error => {
        this.toastr.error(error.errorMessage, 'Failed');
        this.loadingCollection = false;
     });
  }
}