import { Injectable } from '@angular/core';
import { LocalStoreService } from './local-store.service';
import { Router } from '@angular/router';
import { of, Observable, combineLatest } from 'rxjs';
import { delay } from 'rxjs/operators';
import { HttpClient } from '@angular/common/http';
import { WebApiProvider } from '../providers';
import { AuditResponse, AuditResourceChangeDetails } from '../models';

export class Audit {
    id: number;
    date: string;
    user: string;
    message: string;
    changeDetails: string;
}


@Injectable({
    providedIn: 'root'
})
export class AuditService {

    public audits: Audit[];
    loadingCollection: boolean;

    constructor(private http: HttpClient, private webApiProvider: WebApiProvider) {

    }

    loadAuditData(): Observable<any> {
        return combineLatest(
            this.getAllAudits(),
            (audits) => {
              this.audits = audits;
            }
        );
    }

    checkAuth() {
        // this.authenticated = this.store.getItem('demo_login_status');
    }

    getuser() {
        return of({});
    }

    getAllAudits() {
        return this.http.get<Audit[]>('api/audits');
    }

    getAuditTrails(resourceType: string, resourceId: string): Observable<AuditResponse> {
        return this.webApiProvider.get('audit-trail/v1/' + resourceType + "/" + resourceId);
    }

    getAuditEventDetail(eventId: string): Observable<AuditResourceChangeDetails> {
        return this.webApiProvider.get('audit-trail/v1/' + eventId );
    }
}
