import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { ClientAssociationGroup } from '../models/client-association-group';
import { WebApiProvider } from '../providers';

@Injectable({ providedIn: 'root' })
export class ClientAssociationGroupService {
  constructor(private webApiProvider: WebApiProvider) { }

  public getAll(): Observable<ClientAssociationGroup[]> {
    return this.webApiProvider.get('client-assoc-grp/v1/list', null);
  }
}
