import { Address } from '../../model/common-model';

export class Financial {
    name: string;
    id: string;
    address: Address;
    email: string;
    primaryPhoneNo: string;
    secondaryPhoneNo: string;
    routingNumber: string;
    status: string;

    public constructor(init?: Partial<Financial>) {
        Object.assign(this, init);
    }
}

export class FinancialResponse {
    totalPages: number;
    totalElements: number;
    content: Financial[];
}
