import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable, BehaviorSubject, Subject, of, combineLatest } from 'rxjs';
import { map, switchMap } from 'rxjs/operators';
import { Address, Attachment } from 'src/app/model/common-model';
import { WebApiProvider } from 'src/app/shared/providers';
import { ToastrService } from 'ngx-toastr';
import { Transporter, TransporterResponse } from '../models/transporter';


@Injectable({
  providedIn: 'root'
})
export class TransporterService {

  public transporters: Transporter[];
  public transporter: Transporter;
  public collectionSize: number;

  public loadingCollection: boolean;
  public loadingData: boolean;

  onTransporterSelected = new BehaviorSubject<any>(null);
  onAdvancedSearchApplied = new BehaviorSubject<any>(null);
  onClearSearchApplied = new BehaviorSubject<any>(null);


  constructor(private http: HttpClient,
    private toastr: ToastrService,
    private webApiProvider: WebApiProvider) {
  }

  loadTransporterData(page: number, size: number): Observable<TransporterResponse> {
    return this.getAllTransporter((page - 1), size);
  }

  create(transporter: Transporter) {
    return this.webApiProvider.post('transporter/v1/', transporter, true);
  }

  update(transporter: Transporter): Observable<Transporter> {
    return this.webApiProvider.put('transporter/v1/' + transporter.id, transporter);
  }

  delete(id: number) {
    return this.webApiProvider.delete('transporter/v1/'+ id, true);
  }

 
  getAllTransporter(page, size): Observable<TransporterResponse> {
    return this.webApiProvider.get<any>('transporter/v1/list/' + page + '/'+ size, true);
  }

  getTransporterById(id): Observable<Transporter> {
    this.loadingData = true;
    return this.getAllTransporterById(id)
      .pipe(map(transporter => {
        this.onTransporterSelected.next({
          transporter: transporter
        });
        this.loadingData = false;
        return transporter;
      }));
  }

  getAllTransporterById(id): Observable<Transporter> {
    return this.webApiProvider.get<Transporter>(`transporter/v1/${id}`);
  }

  performAdvancedSearch(httpOptions?: any): void {
    this.loadingCollection = true;
    this.webApiProvider.getwithSearchParam('transporter/v1/list/0/6', httpOptions.params)
    .subscribe (response => {
      if (response) {
        this.onAdvancedSearchApplied.next({
          transporters: response
        });
        this.loadingCollection = false;
      }
    },
     error => {
        this.toastr.error(error.errorMessage, 'Failed');
        this.loadingCollection = false;
     });
  }
}

