import { ClientAssociationGroup } from './../../../shared/models/client-association-group';
import { Component, Input, OnInit, ViewChild, ElementRef } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { ToastrService } from 'ngx-toastr';
import { Action } from 'src/app/shared/common/action';
import { ClientAssociationGroupService } from './../../../shared/services/client-association-group.service';
import { RetailerCompany } from 'src/app/shared/models/retailer-company';
import { RetailerCompanyService } from 'src/app/shared/services/retailer-company.service';
import { number } from 'ngx-custom-validators/src/app/number/validator';


@Component({
  selector: 'app-retailer-company-editor',
  templateUrl: './retailer-company-editor.component.html',
  styleUrls: ['./retailer-company-editor.component.scss']
})
export class RetailerCompanyEditorComponent implements OnInit {

  @Input() companyId: number;
  @Input() action: Action;
  @ViewChild('logoname')
  logoname: ElementRef;

  header = '';
  submitted = false;
  companyForm: FormGroup;
  showProgress = false;

  // Logo
  imageSrc: any;
  imageAsString: string;
  mimeType: string;
  imageFileName: string;
  invalidFileType: boolean;

  clientAssociationGroups: ClientAssociationGroup[];

  phoneMask = ['(', /[1-9]/, /\d/, /\d/, ')', ' ', /\d/, /\d/, /\d/, '-', /\d/, /\d/, /\d/, /\d/];

  public retCompany: RetailerCompany = new RetailerCompany();

  constructor(private retailerCompanyService: RetailerCompanyService,
    private clientAssociationGroupService: ClientAssociationGroupService,
    private router: Router,
    private route: ActivatedRoute,
    private fb: FormBuilder,
    private toastr: ToastrService) {
    this.setForm(this.retCompany);
  }

  ngOnInit(): void {
    this.header = this.action === Action.ADD ? 'New Retailer Company' : 'Update Retailer Company';

    this.route.params.subscribe(params => this.companyId = params['id']);

    if (this.companyId) {
      this.showProgress = true;
      this.retailerCompanyService.getById(this.companyId).subscribe(
        res => {
          this.retCompany = res;
          this.setForm(this.retCompany);
        }
      );
    }

    // get association groups
    this.getAllClientAssociationGroups();
  }

  cancel() {
    this.router.navigate(['entities/retailer-company']);
  }


  async setForm(company: RetailerCompany) {
    this.companyForm = this.fb.group({
      id: company.id ? company.id : null,
      name: [company.name, [Validators.required]],
      clientAssociationGroup: company.clientAssociationGroup,
      address: this.fb.group({
        addressLine: company.address ? company.address.addressLine : null,
        city: company.address ? company.address.city : null,
        state: company.address ? company.address.state : null,
        country: company.address ? company.address.country : null,
        postCode: company.address ? company.address.postCode : null
      }
      ),
      primPhNo: [company.primPhNo ? company.primPhNo : null,
      [Validators.required]], //, Validators.pattern('^\\([0-9]{3}\\)\\s?[0-9]{3}-[0-9]{4}$')
      secPhNo: [company.secPhNo ? company.secPhNo : null, []],
      email: [company.email, [Validators.required, Validators.email]],
      mimeType: company.mimeType,
      imageName: company.imageName,
      image: company.image
    });
    console.log(this.showProgress)
    this.showProgress = false;
  }

  onFileChange(files: FileList) {
    const reader = new FileReader();
    if (files && files.length > 0) {
      const file = files[0];

      this.logoname.nativeElement.innerText = Array.from(files)
        .map(f => f.name);

      reader.onload = () => {
        this.imageSrc = reader.result;
        this.imageAsString = (<string>reader.result).split(',')[1];
      }
      this.mimeType = files[0].type;
      if (this.mimeType.match(/image\/*/) == null) {
        this.invalidFileType = true;
        this.imageSrc = null;
        return;
      }
      console.log('image', this.imageSrc);
      this.imageFileName = file.name;
      this.invalidFileType = false;
      reader.readAsDataURL(file);
    }
  }

  onOwnerChange(event) {

  }

  getAllClientAssociationGroups() {
    this.clientAssociationGroupService.getAll().subscribe(res => {
      console.log(res);
      this.clientAssociationGroups = res;
      console.log(this.clientAssociationGroups);
    },
      err => {
        console.log(err);
      }
    );
  }

  save() {
    this.submitted = true;
    console.log(this.companyForm);
    if (this.companyForm.invalid) {
      console.log('Invalid form');
      return;
    }

    this.showProgress = true;
    const group = this.clientAssociationGroups.find(x => x.id == this.f.clientAssociationGroup.value);
    this.f.clientAssociationGroup.setValue(group);
    this.f.mimeType.setValue(this.mimeType);
    this.f.imageName.setValue(this.imageFileName);
    this.f.image.setValue(this.imageAsString);

    const company: RetailerCompany = <any>this.companyForm.value;
    company.address = this.f.address.value;
    company.primPhNo = this.f.primPhNo.value.replace(/[()]/g, '').replace(/\s/g, '').replace(/-/, '');
    company.status = 'ACTIVE';
    console.log('group', group);
    company.clientAssociationGroup = group;
    let response;
    let msg;
    if (Action.ADD == this.action) {
      response = this.retailerCompanyService.saveCompany(company);
      msg = 'Retailer company successfully created.';
    } else {
      response = this.retailerCompanyService.updateCompany( Number(company.id) , company);
      msg = 'Retailer company successfully updated.';

    }

    response.subscribe(
      result => {
        this.retCompany = result;
        this.showProgress = false;
        this.toastr.success(msg);
      },
      error => {
        console.log(error);
        this.showProgress = false;
        this.toastr.error('Something went wrong. Please try again.');
      }
    );

  }


  get f() { return this.companyForm.controls; }
}
