import { Observable, BehaviorSubject } from 'rxjs';
import { WebApiProvider } from '../providers';
import { Injectable } from '@angular/core';
import { RetailerCompany } from '../models/retailer-company';
import { combineLatest } from 'rxjs';
import { map } from 'rxjs/operators';

@Injectable({ providedIn: 'root' })
export class RetailerCompanyService {

    public companies: RetailerCompany[];
    public company: RetailerCompany;
    public onRetailerCompanySelected = new BehaviorSubject<any>(null);
    public loadingCollection: boolean;
    constructor(private webApiProvider: WebApiProvider) { }

    public saveCompany(company: RetailerCompany): Observable<RetailerCompany> {
        return this.webApiProvider.post('retailer-company/v1', company, true);
    }

    public updateCompany(id: number, company: RetailerCompany): Observable<RetailerCompany> {
        return this.webApiProvider.put('retailer-company/v1/' + id, company);
    }

    public deleteCompany(id: string): Observable<RetailerCompany> {
        return this.webApiProvider.delete('retailer-company/v1/' + id, null);
    }

    public getAllWithPagination(page: number, size: number): Observable<RetailerCompany[]> {
        return this.webApiProvider.get<RetailerCompany[]>('retailer-company/v1/list/' + page + '/' + size, null);
    }

    public getById(id: number): Observable<RetailerCompany> {
        return this.webApiProvider.get('retailer-company/v1/' + id);
    }

    public getAll(): Observable<RetailerCompany[]> {
        return this.webApiProvider.get<RetailerCompany[]>('retailer-company/v1/list', null);
    }

    public getRetailerCompanyById(id): Observable<RetailerCompany> {
        this.loadingCollection = true;
        return this.getById(id)
            .pipe(map(company => {
                this.onRetailerCompanySelected.next({
                    company: company
                });
                this.loadingCollection = false;
                return company;
            }));
    }

   loadRetailerCompanyData(): Observable<any> {
    return combineLatest(
        this.getAll(), 
        (result) => {
            this.companies = result;
        }
    );
  }


}
