import { BaseInfo } from "./common-info";
import { Attachment } from "src/app/model/common-model";
import { Depot } from "./depot";
import { Retailer } from "src/app/views/entities/retailer/retailer.service";
import { ShippingTruckInfo } from "./truck";

export class Dispatch extends BaseInfo {
    attachment: Attachment;
    customerName: string;
    depot: BaseInfo;
    dispatchOrderNumber: string;
    dispatchTrucks: DispatchTruck[];
    executionDate: Date;
    fromClient: BaseInfo;
    interDepotTransfer: boolean;
    productType: BaseInfo;
    status: string;
    toClient: BaseInfo;
    workflowStatus: string;
}



export class DispatchTruck {
    id: number;
    collectionOrderNo: string;
    destination: string;
    destinationDepot: Depot;
    driverName: string;
    meterTicketNo: string;
    quantity1: number;
    quantity2: number;
    quantity3: number;
    quantity4: number;
    quantity5: number;
    quantity6: number;
    quantity7: number;
    quantity8: number;
    retailer: Retailer;
    transporterName: string;
    truckInfo: ShippingTruckInfo;
}


export class DispatchResponse {
    totalPages: number;
    totalElements: number;
    content: Dispatch[];
}



