import { HttpEvent, HttpHandler, HttpInterceptor, HttpRequest, HttpErrorResponse } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable, throwError, of } from 'rxjs';
import { catchError } from 'rxjs/operators';
import { Router } from '@angular/router';
import { LocalStoreService } from '../services/local-store.service';
// import { ToastaService } from 'ngx-toasta';
// import { CommonComponent } from 'src/app/layout/dashboard/common.component';

// extends CommonComponent
@Injectable()
export class TokenInterceptor implements HttpInterceptor {
  constructor(private router: Router,
    private localStorage: LocalStoreService
    // , public ts: ToastaService
  ) {
    // super(ts);
  }
  intercept(request: HttpRequest<any>, next: HttpHandler):
    Observable<HttpEvent<any>> {
    console.log('Adding token ..');
    if (this.localStorage.getItem('isAuthenticated') && this.localStorage.getItem('token')) {
      let token = this.localStorage.getItem('token');
      token = token.toString().replace(/^""(.*)"$/, '$1');
      request = request.clone({ headers: request.headers.set('Authtoken',  token)});
      return next.handle(request).pipe(catchError((err: HttpErrorResponse) => {
        if (err.status === 401) {
          console.log('User not authorized !!', err.error);
          localStorage.setItem('token', '');
          localStorage.setItem('isLoggedin', 'false');
          if (localStorage.getItem('isLoggedin') === 'true') {
            this.router.navigate(['/not-found']);
          } else {
            this.router.navigate(['/login']);
          }
        } else if (err.status === 0) {
          console.log('Could not contact server !!', err.message);
          // this.toastError('Could not contact server !! ');
        } else {
          this.router.navigate(['/login']);
        }
        return throwError(err);
      }));
    } else {
      return next.handle(request);
    }
  }
}
