import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable, BehaviorSubject, Subject, of, combineLatest } from 'rxjs';
import { map, switchMap } from 'rxjs/operators';
import { Address, Attachment } from 'src/app/model/common-model';
import { WebApiProvider } from 'src/app/shared/providers';
import { ToastrService } from 'ngx-toastr';
import { Financial, FinancialResponse } from '../models/financial';


@Injectable({
  providedIn: 'root'
})
export class FinancialService {

  public financials: Financial[];
  public financial: Financial;
  public collectionSize: number;

  onContactSelected = new BehaviorSubject<any>(null);

  onFinancialSelected = new BehaviorSubject<any>(null);
  onAdvancedSearchApplied = new BehaviorSubject<any>(null);
  onClearSearchApplied = new BehaviorSubject<any>(null);

  onChatsUpdated = new Subject<any>();
  loadingCollection: boolean;

  constructor(private http: HttpClient,
    private toastr: ToastrService,
    private webApiProvider: WebApiProvider) {
  }

  loadFinancialData(page: number, size: number): Observable<FinancialResponse> {
    return this.getAllFinancials((page - 1), size);
  }

  create(financial: Financial) {
    return this.webApiProvider.post('fi/v1/', financial, true);
  }

  update(financial: Financial): Observable<Financial> {
    return this.webApiProvider.put('fi/v1/' + financial.id, financial);
  }

  delete(id: number) {
    return this.webApiProvider.delete('fi/v1/' + id, true);
  }

  getAllFinancials(page, size): Observable<FinancialResponse> {
    return this.webApiProvider.get<any>('fi/v1/list/' + page + '/' + size, true);
  }

  getFinancialById(id): Observable<Financial> {
    this.loadingCollection = true;
    return this.getAllFinanancialById(id)
      .pipe(map(financial => {
        this.onFinancialSelected.next({
          financial: financial
        });
        this.loadingCollection = false;
        return financial;
      }));
  }

  getAllFinanancialById(id): Observable<Financial> {
    return this.webApiProvider.get<Financial>(`fi/v1/${id}`);
  }

  performAdvancedSearch(query: string): void {
    this.loadingCollection = true;
    this.webApiProvider.get<FinancialResponse>('fi/v1/list/0/6' + query, true)
    .subscribe (response => {
      if (response) {
        this.onAdvancedSearchApplied.next({
          financials: response
        });
        this.loadingCollection = false;
      }
    },
     error => {
        this.toastr.error(error.errorMessage, 'Failed');
        this.loadingCollection = false;
     });
  }
}

