import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable, BehaviorSubject, Subject, of, combineLatest } from 'rxjs';
import { map, switchMap } from 'rxjs/operators';
import { Address, Attachment } from 'src/app/model/common-model';
import { WebApiProvider } from 'src/app/shared/providers';
import { ToastrService } from 'ngx-toastr';
import { Dispatch, DispatchResponse} from '../models/dispatch';


@Injectable({
  providedIn: 'root'
})
export class DispatchService {

  public dispatches: Dispatch[];
  public Dispatch: Dispatch;
  public collectionSize: number;

  onDispatchSelected = new BehaviorSubject<any>(null);
  onAdvancedSearchApplied = new BehaviorSubject<any>(null);
  onClearSearchApplied = new BehaviorSubject<any>(null);

  public loadingCollection: boolean;
  public loadingData: boolean;

  constructor(private http: HttpClient,
    private toastr: ToastrService,
    private webApiProvider: WebApiProvider) {
  }

  loadDispatchData(page: number, size: number): Observable<DispatchResponse> {
    return this.getAllDispatches((page - 1), size);
  }

  create(Dispatch: Dispatch) {
    return this.webApiProvider.post('fi/v1/', Dispatch, true);
  }

  update(Dispatch: Dispatch): Observable<Dispatch> {
    return this.webApiProvider.put('fi/v1/' + Dispatch.id, Dispatch);
  }

  delete(id: number) {
    return this.webApiProvider.delete('fi/v1/' + id, true);
  }

  getAllDispatches(page, size): Observable<DispatchResponse> {
    return this.webApiProvider.get<any>('dispatch/v1/list/' + page + '/' + size, true);
  }

  async selectDispatch(dispatch: Dispatch) {
    this.loadingCollection = true;
    this.loadingData = true;
    await this.delay(2000);
    this.onDispatchSelected.next({
      dispatch: dispatch
    });
    this.loadingData = false;
  }

  private delay(ms: number) {
    return new Promise(resolve => setTimeout(resolve, ms));
  }

  getDispatch() : Observable<any> {
    return this.onDispatchSelected.asObservable();
  }

  getAllDispatchById(id): Observable<Dispatch> {
    return this.webApiProvider.post<Dispatch>(`dispatch/v1/dispatch/view/${id}`, null, true);
  }

  performAdvancedSearch(query: string): void {
    this.loadingCollection = true;
    this.webApiProvider.get<DispatchResponse>('fi/v1/list/0/6' + query, true)
    .subscribe (response => {
      if (response) {
        this.onAdvancedSearchApplied.next({
          dispatches: response
        });
        this.loadingCollection = false;
      }
    },
     error => {
        this.toastr.error(error.errorMessage, 'Failed');
        this.loadingCollection = false;
     });
  }
}

