import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable, BehaviorSubject, Subject, of, combineLatest } from 'rxjs';
import { map, switchMap } from 'rxjs/operators';
import { Address, Attachment } from 'src/app/model/common-model';
import { WebApiProvider } from 'src/app/shared/providers';
import { ToastrService } from 'ngx-toastr';
import { TankConfiguration as TankConfiguration, TankConfigurationResponse } from '../models/tank-configuration';


@Injectable({
  providedIn: 'root'
})
export class TankConfigurationService {

  public tankConfigurations: TankConfiguration[];
  public tankConfiguration: TankConfiguration;
  public collectionSize: number;

  onContactSelected = new BehaviorSubject<any>(null);

  onTankConfigurationSelected = new BehaviorSubject<any>(null);
  onAdvancedSearchApplied = new BehaviorSubject<any>(null);
  onClearSearchApplied = new BehaviorSubject<any>(null);

  onChatsUpdated = new Subject<any>();
  loadingCollection: boolean;
  loadingData: boolean;

  constructor(private http: HttpClient,
    private toastr: ToastrService,
    private webApiProvider: WebApiProvider) {
  }

  loadTankConfigurationData(page: number, size: number): Observable<TankConfigurationResponse> {
    return this.getAllTankConfigurations((page - 1), size);
  }

  create(tank: TankConfiguration) {
    return this.webApiProvider.post('tank/v1/', tank, true);
  }

  update(tank: TankConfiguration): Observable<TankConfiguration> {
    return this.webApiProvider.put('tank/v1/' + tank.id, tank);
  }

  delete(id: number) {
    return this.webApiProvider.delete('tank/v1/' + id, true);
  }

  getAllTankConfigurations(page, size): Observable<TankConfigurationResponse> {
    return this.webApiProvider.get<any>('tank/v1/list/' + page + '/' + size, true);
  }

  getTankConfigurationById(id): Observable<TankConfiguration> {
    this.loadingData = true;
    return this.getAllFinanancialById(id)
      .pipe(map(tank => {
        this.onTankConfigurationSelected.next({
          tank: tank
        });
        this.loadingData = false;
        return tank;
      }));
  }

  getAllFinanancialById(id): Observable<TankConfiguration> {
    return this.webApiProvider.get<TankConfiguration>(`tank/v1/${id}`);
  }

  performAdvancedSearch(query: string): void {
    this.loadingCollection = true;
    this.webApiProvider.get<TankConfigurationResponse>('tank/v1/list/0/6' + query, true)
    .subscribe (response => {
      if (response) {
        this.onAdvancedSearchApplied.next({
          financials: response
        });
        this.loadingCollection = false;
      }
    },
     error => {
        this.toastr.error(error.errorMessage, 'Failed');
        this.loadingCollection = false;
     });
  }
}

