import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { BehaviorSubject, combineLatest, Observable, Subject } from 'rxjs';
import { map } from 'rxjs/operators';
import { Address, Attachment } from 'src/app/model/common-model';

export class Retailer {
  id: number;
  name: string;
  retailerCompany: string;
  registrationNumber: string;
  
  primaryPhoneNo: string;
  secondaryPhoneNo : string;
  email: string;
  primaryIMEI: string;
  secondaryIMEI: string;

  status: string;
  address: Address;
  stationManagerName: string;
  assistantStationManagerName: string;
  contractId: string;
  contractStartDate: Date;
  contractEndDate: Date;
  region: string;
  attachment: Attachment;

  public constructor(init?: Partial<Retailer>) {
    Object.assign(this, init);
  }
}



export class RetailerResponse {
  totalPages: number;
  totalElements: number;
  content: Retailer[];
}

@Injectable({
  providedIn: 'root'
})
export class RetailerService {

  public retailers: Retailer[];
  public retailer: Retailer;

  onContactSelected = new BehaviorSubject<any>(null);
  // onUserUpdated = new Subject<User>();

  onChatSelected = new BehaviorSubject<any>(null);
  onRetailerSelected = new BehaviorSubject<any>(null);
  onAdvancedSearchApplied = new BehaviorSubject<any>(null);

  onChatsUpdated = new Subject<any>();
  loadingCollection: boolean;

  constructor(private http: HttpClient) {

  }

  loadRetailerData(): Observable<any> {
    return combineLatest(
      this.getAllRetailers(),
      (retailers) => {
        this.retailers = retailers;
      }
    );
  }


  searchRetailer(retailer: Retailer): Observable<any> {
    return combineLatest(
      this.getAllRetailers(),
      (retailers) => {
        this.retailers = retailers;
      }
    );
  }

  getAllRetailers(): Observable<Retailer[]> {
    return this.http.get<Retailer[]>('api/retailer');
  }

  getRetailerById(id): Observable<Retailer> {
    this.loadingCollection = true;
    return this.getRetailerbyId(id)
      .pipe(map(retailer => {
        this.onRetailerSelected.next({
          retailer: retailer
        });
        this.loadingCollection = false;
        console.log('service - ', retailer);
        return retailer;
      }));
  }

  autoReply(chat) {
    setTimeout(() => {
      this.onChatsUpdated.next(chat);
    }, 1500);
  }

  getRetailerbyId(id): Observable<Retailer> {
    return this.http.get<Retailer>(`api/retailer/${id}`);
  }

  performAdvancedSearch(company: Retailer): Observable<Retailer[]> {
    this.loadingCollection = true;
    return this.http.post<Retailer[]>('api/retailer/search', company)
      .pipe(map(companies => {
        this.onAdvancedSearchApplied.next({
          companies: companies
        });
        this.loadingCollection = false;
        return companies;
      }));
  }

}

