import {HttpClient} from '@angular/common/http';
import {Injectable} from '@angular/core';
import {environment} from 'src/environments/environment';

import {HttpProvider} from './http.provider';


@Injectable()
export class WebApiProvider extends HttpProvider {
  constructor(http: HttpClient) {
    super(http);
  }

  formatUrl(relativeUrl: string): string {
    relativeUrl = environment.appUrl + relativeUrl;
    relativeUrl = super.formatUrl(relativeUrl);
    return relativeUrl;
  }

  public formatAuthUrl(relativeUrl: string): string {
    relativeUrl = environment.authUrl + relativeUrl;
    relativeUrl = super.formatUrl(relativeUrl);
    return relativeUrl;
  }

}
