import { Component, OnInit, Input } from '@angular/core';
import { Action } from 'src/app/shared/common/action';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { TankConfiguration, ProductType } from 'src/app/shared/models/tank-configuration';
import { TankConfigurationService } from 'src/app/shared/services/tank-configuration.service';
import { Router, ActivatedRoute } from '@angular/router';
import { ToastrService } from 'ngx-toastr';
import { Observable } from 'rxjs';
import { ProductService } from 'src/app/shared/services/product.service';
import { timeout } from 'rxjs/operators';
import { ProductTypeService } from 'src/app/shared/services/product-type.service';

@Component({
  selector: 'app-tank-editor',
  templateUrl: './tank-editor.component.html',
  styleUrls: ['./tank-editor.component.scss']
})
export class TankEditorComponent implements OnInit {

  @Input() tankId: number;
  @Input() action: Action;

  header = '';
  showProgress = false;
  submitted = false;

  attachmentFileName = '';
  waterAttachmentFileName = '';

  // To validate if any services is selected during submitting the form.
  noServicesSelected = false;

  tankForm: FormGroup;
  loading: boolean;

  productTypes: ProductType[];

  tankTypes = [
    {
      value: 'VERTICAL',
      display: 'Vertical tank'
    },
    {
      value: 'HORIZONTAL',
      display: 'Horizontal tank'
    },
    {
      value: 'UNDERGROUND',
      display: 'Underground tank'
    },
    {
      value: 'SPHERICAL',
      display: 'Spherical tank'
    },
  ];

  tankRoofTypes = [
    {
      value: 'FIXED',
      display: 'Fixed'
    },
    {
      value: 'FLOATING',
      display: 'Floating'
    },
    {
      value: 'INTERNAL_FLOATING',
      display: 'Internal floating'
    },
    {
      value: 'EXTERNAL_FLOATING',
      display: 'External floating'
    },
  ];

  public tank: TankConfiguration = new TankConfiguration({
    status: 'ACTIVE',
    prodType: {id: null, name: '', description: ''}
  });

  constructor(public service: TankConfigurationService,
    private productService: ProductTypeService,
    private router: Router,
    private route: ActivatedRoute,
    private fb: FormBuilder,
    private toastr: ToastrService) {
      this.setForm(this.tank);
    }

  ngOnInit(): void {
    this.header = this.action === Action.ADD || this.action === Action.CLONE ? 'New Tank Configuration ' : 'Update Tank Configuration';
    this.route.params.subscribe(params => this.tankId = params['id']);

    if (this.tankId) {
      this.showProgress = true;
      this.service.getTankConfigurationById(this.tankId).subscribe(
        res => {
          debugger;
          this.tank = res;
          this.loadProductTypes();
          if (this.isClone()) {
            this.setFormForClone(this.tank);
            this.showProgress = false;
          } else {
            this.setForm(this.tank);
            this.showProgress = false;
          }
        },
        error => {
          console.log('Error occured: ' + error);
        }
      );
    }
  }

  async loadProductTypes() {
    this.productService.getProductTypes()
    .subscribe(
      response => {
        if (response) {
          this.productTypes = response;
          this.showProgress = false;
        }
      },
      error => {
        const err = JSON.parse(error.error);
        this.toastr.error(err.errorMessage, 'Failed', {timeOut: 4000});
      }
    );
  }

  isClone(): boolean {
    return this.action === Action.CLONE;
  }

  cancel() {
    this.router.navigate(['dt/tank']);
  }

  get f() { return this.tankForm.controls; }


  async setForm(tank: TankConfiguration) {
    this.tankForm = this.fb.group({
      id: this.action === Action.EDIT ? tank.id : null,
      status: tank.status,
      attachment:	tank.attachment ? tank.attachment : null,
      balance: tank.balance,
      calibDate: tank.calibDate,
      capacity: tank.capacity,
      capacityLiters: tank.capacityLiters,
      description: tank.description,
      expDate: tank.expDate,
      level: tank.level,
      prodType:	this.fb.group({
        id: tank.prodType.id,
        name: tank.prodType.name,
        description: tank.prodType.description
       }
      ),
      roofWeight: tank.roofWeight,
      tankNominalHeight: tank.tankNominalHeight,
      tankNominalHeightLiters: tank.tankNominalHeightLiters,
      tankNumber: tank.tankNumber,
      tankRoofType: tank.tankRoofType,
      tankSafeHeight: tank.tankSafeHeight,
      tankSafeHeightLiters: tank.tankSafeHeightLiters,
      tankType: tank.tankType,
      waterattachment: tank.waterattachment ? tank.waterattachment : null
    });
    this.showProgress = false;
  }

  async setFormForClone(tank: TankConfiguration) {
    this.tankForm = this.fb.group({
      // id: null,
      // name: ['', [Validators.required]],
      // routingNumber: ['', [Validators.required]],
      // address: this.fb.group({
      //   addressLine: tank.address.addressLine,
      //   city: tank.address.city,
      //   state: tank.address.state,
      //   country: tank.address.country,
      //   postCode: tank.address.postCode
      //  }
      // ),
      // primaryPhoneNo: tank.primaryPhoneNo,
      // secondaryPhoneNo: tank.secondaryPhoneNo,
      // email: [tank.email,  [Validators.required, Validators.email]],
      // status: tank.status
    });
    this.showProgress = false;
  }


  save() {
    this.submitted = true;
    const tank: TankConfiguration = <any>this.tankForm.value;

    if (this.tankForm.invalid) {
      return;
    }
    this.showProgress = true;
    let resp: Observable<any>;
    let alertMessage =  '';

    if (this.action === Action.ADD || this.action === Action.CLONE) {
      resp = this.service.create(tank);
      alertMessage = 'TankConfiguration created successfully !';
      this.service.loadTankConfigurationData(0, 6);
    }

    if (this.action === Action.EDIT) {
      resp = this.service.update(tank);
      alertMessage = 'TankConfiguration updated successfully !';
    }

    resp.subscribe(response => {
        if (response) {
          this.toastr.success(alertMessage, 'Success', { timeOut: 30000 });
          this.service.onTankConfigurationSelected.next(response.data);
          this.showProgress = false;
        } else {
          this.toastr.error(response.errorMessage, 'Failed', { timeOut: 30000});
          this.showProgress = false;
        }
      },
      error => {
        this.toastr.error(error.errorMessage, 'Failed', { timeOut: 30000});
        this.showProgress = false;
      }
    );
  }

  onFileChange(event) {
    this.attachmentFileName = '';
    const reader = new FileReader();
    if (event.target.files && event.target.files.length > 0) {
      const file = event.target.files[0];
      reader.readAsDataURL(file);
      reader.onload = () => {
        this.tankForm.get('attachment').setValue({
          id: null,
          name: file.name,
          content: (<string>reader.result).split(',')[1]
        });
      };
    }
  }

}
