import { Address, Attachment } from '../../model/common-model';
import { CommonInfo } from './common-info';

export class TankConfiguration extends CommonInfo {
    attachment:	Attachment;
    balance: number;
    calibDate: Date;
    capacity: number;
    capacityLiters: number;
    description: string;
    expDate: Date;
    level: number;
    paramCalib:	string;
    prodType:	ProductType;
    roofWeight: number;
    status: string;
    tankNominalHeight: number;
    tankNominalHeightLiters: number;
    tankNumber: string;
    tankRoofType: string;
    tankSafeHeight: number;
    tankSafeHeightLiters: number;
    tankType: string;
    version: number;
    waterattachment: Attachment;

    public constructor(init?: Partial<TankConfiguration>) {
        super();
        Object.assign(this, init);
    }
}

export class TankConfigurationResponse {
    totalPages: number;
    totalElements: number;
    content: TankConfiguration[];
  }

export class ProductType {
    description: string;
    id:	number;
    name: string;
}

export enum TankRoofType {
    FIXED = 'Fixed',
    FLOATING = 'Floating',
    INTERNAL_FLOATING = 'Internal floating',
    EXTERNAL_FLOATING = 'External Floating'
}

export enum TankType {
    VERTICAL = 'Vertical',
    HORIZONTAL = 'Horizontal',
    UNDERGROUND = 'Underground',
    SPHERICAL = 'Spherical'
}

export class ProductTypeResponse {
    productTypes: ProductType[];
}
